import MailOutlined from '@ant-design/icons/lib/icons/MailOutlined';
import PhoneOutlined from '@ant-design/icons/lib/icons/PhoneOutlined';
import Row from 'antd/lib/grid/row';
import Paragraph from 'antd/lib/typography/Paragraph';
import Text from 'antd/lib/typography/Text';
import Typography from 'antd/lib/typography/Typography';
import { graphql, Link, useStaticQuery } from 'gatsby';
import React from 'react';
import { CategoryTagInfo, Post } from '../../contracts/post';
import './sidebar.scss';

export const Sidebar = () => {

	const data = useStaticQuery(graphql`
	query {
		allWpPost(limit: 5) {
			nodes {
			  slug
			  title
			}
		}
		allWpCategory(filter: {count: {gt: 0}}) {
		  nodes {
			slug
			name
			count
		  }
		}
		allWpTag(filter: {count: {gt: 1}}) {
		  nodes {
			slug
			name
			count
		  }
		}
	  }
  `);

	return (
		<>
			<Row justify="center">
				<img width="149px" height="40px" src="/dvs_logo.png" alt="Logo DevScope" />
			</Row>
			<Row>
				<Typography>
					<Paragraph>
						As SharePoint/Office365 and .NET Consultants we are available for training, assessments, implementations and workshops on any project. Call us or send us an email if you have any questions.
					</Paragraph>
					<Row justify="center">
						<a href={'mailto:ricardo.calejo@devscope.net'}>
							<Text className="secondaryColor fontBold">
								<MailOutlined /> Portal's Team
						</Text>
						</a>
					</Row>
					<Row justify="center">
						<Text className="secondaryColor">
							<PhoneOutlined /> +351 223 751 350
						</Text>
					</Row>
				</Typography>
			</Row>
			<Row>
				<Typography>
					<div className="title-recentPosts">Recent Posts</div>
					<div className="list-recentPosts">
						{data && data.allWpPost && data.allWpPost.nodes.map((post: Post) => {
							return (
								<Row className="row-recentPosts" key={post.slug}>
									<Link to={`/${post.slug}`} title={post.slug}>
										<Text className="list-secondaryColor">
											{post.title}
										</Text>
									</Link>
								</Row>
							);
						})}
					</div>
				</Typography>
			</Row>
			<Row>
				<Typography>
					<div className="title-recentPosts">Categories</div>
					<div className="list-recentPosts">
						{data && data.allWpCategory && data.allWpCategory.nodes.map((category: CategoryTagInfo) => {
							return (
								<Row className="row-recentPosts" key={category.slug}>
									<Link to={`/category/${category.slug}`} title={category.slug}>
										<Text className="list-secondaryColor">
											{category.name} <Text className="text-greyColor">({category.count})</Text>
										</Text>
									</Link>
								</Row>
							);
						})}
					</div>
				</Typography>
			</Row>
			<Row>
				<Typography>
					<div className="title-recentPosts">Tags</div>
					<div className="list-recentPosts">
						{data && data.allWpTag && data.allWpTag.nodes.map((tag: CategoryTagInfo) => {
							let rangeCount = 0;
							switch (true) {
								case tag.count <= 2: {
									rangeCount = 1;
									break;
								}
								case tag.count <= 3: {
									rangeCount = 2;
									break;
								}
								case tag.count <= 4: {
									rangeCount = 3;
									break;
								}
								case tag.count <= 5: {
									rangeCount = 4;
									break;
								}
								case tag.count <= 10: {
									rangeCount = 5;
									break;
								}
								default: {
									rangeCount = 6;
									break;
								}
							}
							return (
								<Link to={`/tag/${tag.slug}`} title={tag.slug} key={tag.slug}>
									<Text className={`list-secondaryColor text-size-${rangeCount}`}>
										{tag.name + ' '}
									</Text>
								</Link>
							);
						})}
					</div>
				</Typography>
			</Row>
		</>
	);
};

export default Sidebar;
